import { Button } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import React from 'react';
import { Color } from '../../styles/styles';

export interface Props {
  classes: {
    loginContainer: string;
    loginForm: string;
    loginInput: string;
    foc: string;
    loginButtonGroup: string;
    loginButton: string;
  };
}

const styles: object = (theme) => ({
  loginContainer: {
    height: 360,
    backgroundColor: Color.secondColor,
  },
  loginForm: {
    display: 'flex',
    flexDirection: 'column',
  },
  loginInput: {
    marginTop: 48,
    backgroundColor: Color.gray,
  },
  loginButtonGroup: {
    display: 'flex',
    justifyContent: 'center',
  },
  loginButton: {
    marginTop: 48,
    backgroundColor: Color.cyan,
    '&:hover': {
      backgroundColor: Color.cyan700,
    },
  },
  [theme.breakpoints.down('sm')]: {
    loginContainer: {
      width: '100%',
    },
  },
  [theme.breakpoints.up('sm')]: {
    loginContainer: {
      width: 800,
      margin: '0 auto',
      marginTop: '3em',
    },
  },
});

const NewTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: Color.cyan700,
    },
    '& .MuiFilledInput-underline::after': {
      borderBottom: `4px solid ${Color.cyan700}`,
    },
  },
})(TextField);

const LoginView = ({ classes }: Props): React.ReactElement => (
  <Container className={classes.loginContainer} maxWidth="sm">
    <form className={classes.loginForm}>
      <NewTextField
        id="email"
        label="Email"
        className={classes.loginInput}
        type="text"
        autoComplete="current-password"
        margin="normal"
        variant="filled"
      />
      <NewTextField
        id="password"
        label="Passowrd"
        className={classes.loginInput}
        type="password"
        autoComplete="current-password"
        margin="normal"
        variant="filled"
      />
      <div className={classes.loginButtonGroup}>
        <Button variant="contained" className={classes.loginButton}>
          Login
        </Button>
      </div>
    </form>
  </Container>
);

export default withStyles(styles)(LoginView);
